import React, { useState } from "react";
import { GedcomParser } from 'gedcom-parser';  

function App() {
  const [file, setFile] = useState(null);
  const [results, setResults] = useState(null);

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    if (!file) {
      alert("Please upload a GEDCOM file.");
      return;
    }
  
    const reader = new FileReader();
  
    reader.onload = async (event) => {
      const gedcomData = event.target.result;
    
      // Ensure the data is a valid string
      if (typeof gedcomData !== 'string') {
        console.error("Invalid GEDCOM data");
        return;
      }
    
      try {
        console.log("Attempting to parse GEDCOM data...");
        
        // Try parsing the GEDCOM data
        const gedcomParser = new GedcomParser();
        const parsedData = gedcomParser.parse(gedcomData);
    
        console.log("Parsed Data:", parsedData);  // Log parsed data for debugging
    
        // Proceed with further logic
        const couplesWithMarriageEvents = extractMarriageEvents(parsedData);
        const response = await fetch(
          "https://us-central1-gendep82-matching.cloudfunctions.net/compareCouples",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ couples: couplesWithMarriageEvents }),
          }
        );
    
        const data = await response.json();
        setResults(data);
      } catch (error) {
        console.error("Error parsing GEDCOM file:", error);
        console.log("GEDCOM Data Preview:", gedcomData.slice(0, 500));  // Log the first 500 characters for troubleshooting
      }
    };

  
    reader.readAsText(file);  // Ensure the file is read as text
  };
  

  const extractMarriageEvents = (parsedData) => {
    // Logic to extract couples with marriage events based on locations from GEDCOM data
    const couples = [];
    // Example: Loop through parsed data to find marriage events
    parsedData.forEach(individual => {
      if (individual.events) {
        const marriage = individual.events.find(event => event.type === "MARR");
        if (marriage && marriage.place) {
          couples.push({
            husbandFirstName: individual.firstName,
            husbandLastName: individual.lastName,
            wifeFirstName: individual.spouse.firstName,
            wifeLastName: individual.spouse.lastName,
            marriagePlace: marriage.place
          });
        }
      }
    });
    return couples;
  };

  return (
    <div>
      <h1>Upload GEDCOM File</h1>
      <form onSubmit={handleSubmit}>
        <input type="file" accept=".ged" onChange={handleFileChange} />
        <button type="submit">Comparer</button>
      </form>

      {results && (
        <div>
          <h2>Rsultats de comparaison:</h2>
          <pre>{JSON.stringify(results, null, 2)}</pre>
        </div>
      )}
    </div>
  );
}

export default App;